import React from 'react';
import './Post/Post.css';
import { Link } from 'react-router-dom';

class Adtop extends React.Component {
    render() {
        return (

            <React.Fragment>
                <section className="top">
                    <div className="contain-30">
                        <div>
                            <h2>
                                <Link to="/"><i className="fa fa-arrow-left "></i>
                                </Link>
                            Olx
                            </h2>
                        </div>
                    </div>
                </section>

            </React.Fragment>

        )
    }
}


export default Adtop;