import React from 'react';
import ComboBox from '../comboBox/ComboBox.jsx'
import RadioBtn from '../radioButtons/Radiobtn.jsx'
import InputBox from '../inputBox/InputBox.jsx'
import ReviewDetails from '../ReviewDetails/ReviewDetails.jsx'
import './Attributes.css'

class AttributeChange extends React.Component {
    render() {

        return (
            <React.Fragment>

                <div className="innerbelowCenterDiv">
                    <h5>INCLUDE SOME DETAILS</h5>
                    <ComboBox Name="Bedrooms " values={['', 1, 2, 3, 4, 5, '6+', 'Studio']} />
                    <ComboBox Name="Bathrooms " values={['', 1, 2, 3, 4, 5, '6+', '7+']} />
                    <RadioBtn Name="Area Unit " values={["Kanal", 'Marla', 'Square Feet', 'Square Meter', 'Square Yards']} />
                    <InputBox Name="Area " count={5} note="" minCount={0} />
                    <RadioBtn Name="Furnised " values={["Yes", 'No']} />
                    <InputBox Name="Ad Title " count={70} minCount={5} para="Mention the key features of your item (e.g. brand, model, age, type)"/>
                    <InputBox Name="Description " count={4096} minCount={20} para="Mention the key features of your item (e.g. brand, model, age, type)"/>
                </div>
                <ReviewDetails/>

            </React.Fragment>
        )
    }
}

export default AttributeChange;