import React from 'react'
import './InputBox.css'
class InputBox extends React.Component {

    constructor(props) {
        super(props)
        this.state = {
            count:0,
            red: false
        }
    }

    render() {

        let { count, red } = this.state;
        let handlechange = (e) => {
           // value=value.replace(/[^\d]/,0);
            let value = e.target.value;
            (value == "" || value.length < this.props.minCount) ?
                this.setState({
                    count: value.length,
                    red: true
                })
                :
                this.setState({
                    count: value.length,
                    red: false
                })
            console.log(value, count)

        }

        return (
            <React.Fragment>
                <div className="inputComponentDiv">
                    <p className={`buttonName ${red ?"colorRed":""}` }>
                        {this.props.Name}*
                    </p>

                    <input maxLength={this.props.count} type="text" className={`inputText  ${(red ) ? 'Red' : ''}`} onChange={(e) => handlechange(e)} />
                    <div className="validateDiv">                        
                        <div className="row bg-white">
                            <div className="col-lg-11">
                                <p className={` ${red ? 'colorRed':'colorPeal'}`}>
                                    {
                                        (this.props.note==="")?
                                        ((red) ? this.props.Name + " is mandatory. Please complete the required field" : ""):
                                        (red) ? `A minimum length of ${this.props.minCount} characters is required. Please edit the field` : this.props.para
                                    }
                                </p>
                            </div>
                            <div className="col-lg-1 ">
                                <p className="countPara">
                                    {count}/{this.props.count}
                                </p>
                            </div>

                        </div>
                    </div>


                </div>
            </React.Fragment>
        )
    }
}

export default InputBox;