import React from 'react'
import './RadioBtn.css'
class RadioBtn extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            options: props.values,
            isClicked: false
        }
    }

    render() {
        let { options,isClicked } = this.state;

        let handleChange = (index) => {
            console.log('btn'+index)
            this.setState({
                isClicked: true
            })

        }
        return (

            <React.Fragment>

                <p className="buttonName">
                    {
                        this.props.Name + '*'
                    }
                </p>
                <div className="buttonDiv">

                    {
                        options.map((value, index) => {
                            return <button onClick={() => handleChange(index)} className="btn btn-outline-secondary" id={`btn${(isClicked) ? index :''}`} >
                                {
                                    value
                                }
                            </button>
                        })
                    }
                </div>

            </React.Fragment>
        )
    }
}

export default RadioBtn;