import React from 'react'
import '../App.css'
import { Link } from 'react-router-dom'



class Links extends React.Component {
constructor(){
    super()
    this.state={
        list:[{itemName:'Mobile Phones', Ref:'/'},
        {itemName:'Cars', Ref:'/'},
        {itemName:'Motor Cycles', Ref:'/'},
        {itemName:'Houses', Ref:'/'},
        {itemName:'TV - Video - Audio', Ref:'/'},
        {itemName:'Land & Plots', Ref:'/'}]
    }
}

    render() {
        const {list}=this.state;
        return (
            <React.Fragment>
                <div className="listContainer">
                    <ul className="list">
                        {
                            list.map((value,index)=>{
                                return <li key ={index}>
                                    <Link className="noDecor" to ={value.Ref}>{value.itemName}</Link>
                                </li>
                            })
                        }
                    </ul>
                </div>


            </React.Fragment>

        )
    }


}

export default Links;