import React from 'react'
import './ComboBox.css'


class ComboBox extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            options: props.values,
            count: 0,
            red:false
        }
    }
    

    render() {
       
        let onChange = (e) => {
            // console.log(e.target.value);
            (e.target.value =="")?this.setState({red:true}):this.setState({red:false})
        }
        let { options, red } = this.state;
        console.log(options)
        // console.log(value)

        let ShowProps = () => {
            for (var i = 0; i < this.props.values.length; i++) {

                console.log(this.props.values[i])

            }

        }

        return (
            <React.Fragment>

                <div className="comboBoxDiv">
                    <p className={`comboBoxName ${red ? "colorRed" : ""}`}>
                        {this.props.Name}*<br />
                        {
                            ShowProps()
                        }
                    </p>

                    <select name="" id="" className={`comboBox ${!red ? "" : "Red"}`} onClick={(e) => onChange(e)}>
                        {
                            options.map((value, index) => {
                                return <option value={value} className='comboBoxOption' key={index}>
                                    {
                                        value
                                    }
                                </option>
                            })
                        }
                    </select>
                    <p className="colorRed">  
                        {
                            (red)? this.props.Name+"is mandatory. Please complete the required field":""
                        }
                    </p>
                </div>

            </React.Fragment>
        )
    }

}

export default ComboBox;
