import React from "react";
import {BrowserRouter as Router,Route} from "react-router-dom";
import Home from '../containers/Home'
import About from '../containers/About'
// import Post from '../containers/Post/Post.jsx'
import ScrollableTabsButtonAuto from '../containers/categories/Categories.jsx'
import Attributes from '../containers/Attributes/Attributes.jsx'
class AppRouter extends React.Component{

    render(){
        return(
            <Router>

            <Route exact path="/" component={Home}/>
            <Route  path="/about" component={About}/>
            {/* <Route  path="/post" component={Post}/> */}
            <Route path="/post" component={ScrollableTabsButtonAuto} />
            <Route  path="/attributes" component={Attributes}/>

            </Router>
        )
    }
}

export default AppRouter;