import React from 'react'
import './ReviewDetails.css'
import image from '../../Images/car.jpg'
import InputBox from '../inputBox/InputBox.jsx'
import Switches from '../Switch.jsx'
class ReviewDetails extends React.Component {

    render() {
        return (
            <React.Fragment>
                <div className="ReviewDetailsDiv">
                    <h5>REVIEW YOUR DETAILS</h5>
                    <div className="row bg-white">
                        <span className="imageSpan col-lg-2">
                            <img src={image} alt="" />
                        </span>
                        <span className="nameSpan col-lg-10">
                            <InputBox className="Name" Name="Name" note="" count={30}/>
                        </span>
                    </div>
                    <div className="verifyAccountDiv">
                        <h5 >
                        Let's verify your account
                        </h5>
                        <p>
                        We will send you a confirmation code by sms on the next step.
                        </p>
                        <input type="text" class="form-control" id="inlineFormInputGroupUsername2" placeholder="Username" />
  
                    </div>
                    <div>
                        <p>

                        </p>
                        <Switches />
                    </div>
                </div>


            </React.Fragment>
        )
    }
}

export default ReviewDetails;